@import "../../sassStyles/_variables";

#backgroundShapes {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/background_shapes.png") no-repeat 0 100%;
}

#pagesContainer {
  position: relative;
  width: calc(100% - 6vw);
  height: 100%;
  margin-left: 6vw;
  transition: 1s cubic-bezier(.86,0,.07,1);
}

.page {
  position: relative;
  width: 100%;
  height: 100vh;
  margin-bottom: 100px;

  &#about-page .content {
      height: 115%;
      width: 90%;
      bottom: -30%;
      right: 0;
      z-index: 1;
      background-color: $primary-middle-light-color-palette;
  }

  &#skills-page .content {
    height: 120%;
    width: 90%;
    bottom: -30%;
    left: -6vw;
    background-color: $primary-light-color-palette;
  }

  &#work-page .content {
    height: 130%;
    width: 90%;
    bottom: -30%;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    background-color: $primary-middle-light-color-palette;
  }

  &#contact-page .content {
    height: 85%;
    width: 85%;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: $primary-light-color-palette;
  }

}

.content {
  position: absolute;
  background-color: $primary-middle-light-color-palette;
  padding: 20px;
  transition: 0.5s ease-in-out;

  .title {
    position: absolute;
    font-size: 4vw;
    font-family: "Josefin Sans", sans-serif;

    &:after {
      content: "";
      width: 100%;
      height: 5px;
      background-color: #ea9a27;
      display: block;
      position: absolute;
      left: 0;
      transition: 0.5s ease-in-out;
    }

    &#about-title {
      top: 50px;
      left: 100px;

    }

    &#skills-title {
      bottom: 0;
      right: calc(6vw + 15%);
      transform: translateY(50%);
    }

    &#work-title {
      top: 50px;
      left: 50%;
      transform: translateX(-50%);
    }

    &#contact-title {
      top: 50px;
      left: 100px;
    }

  }

  #about-text {
    position: absolute;
    left: 100px;
    top: 40%;
    width: 45%;
    transform: translateY(-50%);
    font-size: 25px;
    line-height: 1.2;
  }

  #profile-picture {
    width: 300px;
    height: 300px;
    position: absolute;
    top: 40%;
    left: 75%;
    transform: translate(-50%, -50%);
    background-color: $secondary-color-palette;
  }


  #works-caroussel {
    position: absolute;
    display: flex;
    top: 20%;
    left: 0;
    justify-content: space-evenly;
    width: 100%;

    .project {
      width: 350px;
      height: 300px;
      background-color: $secondary-color-palette;
      border: 2px solid $primary-dark-color-palette;
      cursor: pointer;
    }

  }

  .contact-text {
    position: absolute;
    left: 100px;
    top: 50%;
    width: 30%;
    transform: translateY(-50%);
    font-size: 25px;
    line-height: 1.2;
  }

  .contact-form {
    position: absolute;
    display: flex;
    width: 50%;
    top: 50%;
    left: 70%;
    transform: translate(-50%, -50%);
    justify-content: space-between;
    flex-wrap: wrap;

    input, textarea, button {
      background-color: #121e26;
      color: white;
      font-size: 18px;
      resize: none;
      font-style: normal;
      font-family: "BarlowCondensed-Light", sans-serif;
      padding: 10px;
      width: 100%;
      height: 100%;

      &:focus  {
        outline: 0;

        +.input-border {
          width: 100%;
        }
      }
    }

    .input-border {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0%;
      height: 3px;
      background-color: $secondary-color-palette;
      transition: 0.2s ease-in-out;
    }

    .name-input, .email-input {
      width: 49%;
      height: 50px;
      position: relative;
    }

    .message-input {
      width: 100%;
      position: relative;
      height: 200px;
      margin: 2% 0;
    }

    .submit-input {
      cursor: pointer;
      height: 50px;
      width: 35%;
      position: relative;
      transform: scale(1);
      transition: 0.1s ease-in-out;

      span:nth-child(1) {
        top: 0;
        left: 0;
        width: 100%;
        height: 3px;
        transform-origin: right;
        transition-delay: 0s;
      }
      span:nth-child(2) {
        top: 0;
        right: 0;
        width: 3px;
        height: 100%;
        transform-origin: bottom;
        transition-delay: 0.1s;
      }
      span:nth-child(3) {
        bottom: 0;
        right: 0;
        width: 100%;
        height: 3px;
        transform-origin: left;
        transition-delay: 0.2s;
      }
      span:nth-child(4) {
        bottom: 0;
        left: 0;
        width: 3px;
        height: 100%;
        transform-origin: top;
        transition-delay: 0.3s;
      }

      &:hover {
        .submit-border {
          transform: scale(1);
        }

        span:nth-child(1) {
          transform-origin: left;
        }
        span:nth-child(2) {
          transform-origin: top;
        }
        span:nth-child(3) {
          transform-origin: right;
        }
        span:nth-child(4) {
          transform-origin: bottom;
        }
      }

      .submit-border {
        transform: scale(0);
        background-color: $secondary-color-palette;
        position: absolute;
        transition: transform 0.1s linear;
      }

      &:active {
        transform: scale(0.95);
        background-color: $secondary-color-palette;
        color: $primary-dark-color-palette;
        font-weight: bold;
      }

    }

  }
}


.mention {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 15px;
  color: #b3b3b3;
}

